<template>
  <div>
    <div style="width: 92%;display:flex;justify-content: end;margin-top: 20px;">
      <img src="http://img.aiimg.com/uploads/userup/0903/260411053014.jpg" alt="uni" style="width: 600px;height: 425px;"/>
    </div>
    <div style="width: 92%;margin-top: 50px;margin-left: 20px;">
      <img src="https://www.bobizs.com/uploads/allimg/200608/1-20060QS246.jpg" alt="uni" style="width: 600px;height: 395px;" />
    </div>
    <div :class="{show:true,show_f:show0}" ref="show0_l" style="width: 92%;display:flex;justify-content: end;">
      <img src="https://pic.ntimg.cn/file/20200623/29652875_181128373035_2.jpg" alt="uni" style="width: 600px;height: 395px;"/>
    </div>
    <div :class="{show:true,show_f:show1}" ref="show1_l" style="width: 92%;margin-top: 50px;margin-left: 20px;">
      <img src="https://help.zhaobiao.cn/image/help/s_ydyrgkf.png" alt="uni" style="width: 600px;height: 395px;" />
    </div>
    <div :class="{secound_f:true,secound:show2}" ref="show2_l" style="width: 92%;display:flex;justify-content: end;margin-top: 50px;">
      <img src="https://img.zcool.cn/community/01ac5f5d4aa7a5a8012187f4e57fe2.jpg@1280w_1l_2o_100sh.jpg" alt="uni" style="width: 650px;height: 395px;" />
    </div>
    <div :class="{secound_f:true,secound:show3}" ref="show3_l" style="width: 92%;margin-top: 50px;margin-left: 20px;">
      <img src="https://img.zcool.cn/community/01f17e554300770000019ae9d63e2e.jpg@1280w_1l_2o_100sh.jpg" alt="uni" style="width: 650px;height: 395px;" />
    </div>
    <div :class="{show:true,show_f:show4}" ref="show4_l" style="width: 92%;display:flex;justify-content: end;margin-top: 50px;">
      <img src="http://www.lanhesheji.com/uploadfile/2015/1024/20151024040636176.jpg" alt="uni" style="width: 600px;height: 395px;" />
    </div>
    <el-affix position="bottom" :offset="30">
      <el-button type="primary" style="position: relative;left: 30px;" @click="back">返回</el-button>
    </el-affix>
  </div>
</template>
<script>

import router from "@/router/router";
import {ElNotification} from "element-plus";

export default {
  name: "about_lw",
  data() {
    return {
      show0: false,
      show1: false,
      show2: false,
      show3: false,
      show4: false,

    }
  },
  mounted() {
    if (this.whe_iphone()) {
      router.replace({
        path: '/phone'
      })
    } else if (sessionStorage.getItem("admin") === null) {
      this.exit();
    }else {
      this.outTime();
      window.addEventListener('scroll', this.handleScroll);
    }

  },
  methods: {
    handleScroll() {
      let center = document.documentElement.clientHeight/2;
      this.show0 = window.pageYOffset >= this.$refs.show0_l.offsetTop - center;
      this.show1 = window.pageYOffset >= this.$refs.show1_l.offsetTop - center;
      this.show2 = window.pageYOffset >= this.$refs.show2_l.offsetTop - center;
      this.show3 = window.pageYOffset >= this.$refs.show3_l.offsetTop - center;
      this.show4 = window.pageYOffset >= this.$refs.show4_l.offsetTop - document.documentElement.clientHeight * 0.57;
    },
    whe_iphone() {
      return navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
    },
    back(){
      router.back();
    },
    outTime(){
      let times = 0;
      let wheClear = setInterval(()=>{
        if(times >= 1800){
          sessionStorage.clear();
          ElNotification({
            title:'提示',
            message:'身份过期,请重新登录！',
            type:'info',
            duration: 0
          });
          this.exit();
          clearInterval(wheClear);
        }
        times++;
      },1000);
    },
    exit(){
      router.replace({
        path:'/'
      })
    },

  }
}
</script>

<style scoped lang="scss">
.show_f {
  transform: translateX(0%) !important;
  opacity: 1 !important;
  transition: .5s;
}

.show {
  transform: translateX(3%);
  opacity: 0;
}

.secound_f {
  transform: translateY(15%);
  opacity: 0;
}

.secound {
  transform: translateY(0%) !important;
  opacity: 1 !important;
  transition: all 1s ease;
}
</style>